<template>
    <yd-layout>
        <div v-show="formFlag">
            <div class="mainContentFluid selectList">
                <div class="fluidWidth">
                    <div class="adviceName">
                        <div class="tripName" :class="{tripName1:applyList.length==1}">
                            {{firstorgname}}{{confirmItem}}
                        </div>
                        <div class="selectAdvice firstBtn" v-if="applyList.length!=1" v-on:click="statusPopup = true;popupArr = applyList;">
                            选择其他出差人
                        </div>
                    </div>
                </div>
            </div>
            <div class="mainContent">
                <div class="contentSradius">
                    <div class="clear"></div>
                    <yd-cell-group>
                        <yd-cell-item>
                            <span slot="left" class="depart">出差地点：</span>
                            <yd-input id="js_addressInput" slot="right" v-model="travelplace" placeholder="请输入您的出差地点"></yd-input>
                        </yd-cell-item>
                    </yd-cell-group>
                    <div class="part_two tripTime">
                        <yd-cell-group>
                            <yd-cell-item arrow>
                                <span slot="left" class="depart">开始时间：</span>
                                <yd-datetime :class="'dateColor'" type="date" v-model="startdate" slot="right"></yd-datetime>
                            </yd-cell-item>
                            <yd-cell-item arrow>
                                <span slot="left" class="depart">结束时间：</span>
                                <yd-datetime :class="'dateColor'" type="date" v-model="enddate" slot="right"></yd-datetime>
                            </yd-cell-item>
                            <!-- <div class="otherTrip" v-show="!showTime || Edate == endSelect">
                                <div class="commonTrip">
                                    结束时间：
                                </div>
                                <div class="endText" @click="openShowTime">
                                    {{endText}}
                                </div>
                            </div>
                            <yd-cell-item arrow v-show="showTime && Edate != endSelect">
                                <span slot="left" class="depart">结束时间：</span>
                                <yd-datetime :start-date="endSelect" ref="datetime" :class="'dateColor'" type="date" v-model="Edate" slot="right"></yd-datetime>
                            </yd-cell-item> -->
                        </yd-cell-group>
                    </div>
                    <yd-cell-group>
                        <yd-cell-item>
                            <span slot="left" class="depart">出差天数：</span>
                            <yd-input id="js_daysInput" slot="right" v-model="inputDay" placeholder="请输入您的出差天数"></yd-input>
                        </yd-cell-item>
                    </yd-cell-group>
                    <div class="otherTrip otherPerson">
                        <div class="commonTrip">
                            随行人员：
                        </div>
                        <div class="selectBtn" @click="$router.push('/tripOrgPerson')" v-if="othertravelnames==''">
                            <yd-navbar-next-icon>请选择您的随行人员</yd-navbar-next-icon>
                        </div>
                        <div class="selectBtn" @click="$router.push('/tripOrgPerson')" v-if="othertravelnames!=''">
                            <div class="pleaseSelect2" v-text="othertravelnames"></div>
                            <yd-navbar-next-icon></yd-navbar-next-icon>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mainContent">
                <div class="contentSradius">
                    <div class="adviceTxt reasonText">
                        <div class="commonAdvice">出差事由：</div>
                        <trsTextarea id="js_reasonInput" placeholder="请填写您的出差事由" v-model="reason"></trsTextarea>
                    </div>
                </div>
            </div>
            <div class="mainContent">
                <div class="contentSradius">
                    <div class="adviceTxt scheduling">
                        <div class="commonAdvice">行程安排：</div>
                        <trsTextarea id="js_reason2Input" :placeholder="placeholder" v-model="reason2"></trsTextarea>
                    </div>
                </div>
            </div>
            <div class="mainContentFluid" v-if="secondFlag == true && aproveList.length != 0">
                <div class="fluidWidth">
                    <div class="adviceLeader">
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="adviceTitle">领导审批意见：</div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div style="width:100%">
                                    <div class="label_box" v-for="item in aproveList">
                                        <div class="range_txt attachment" v-if="item.userName1!=''">{{item.userName1}}：</div>
                                        <div class="range_txt attachment" v-text='item.neirong'></div>
                                        <div class="range_txt issueName">
                                            <div class="range issueTime" v-text='getDay(item.submitDate)'></div>
                                        </div>
                                    </div>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                    </div>
                </div>
            </div>
        </div>
        <yd-popup v-model="statusPopup" v-show="formFlag" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="statusPopup = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup = false">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" v-for="(item,index) in popupArr" v-bind:class="{active:statusRadio==index}" v-on:click="statusRadio=index;applicantname=item.empname;confirmItem = applicantname;applicantid=item.operatorid;firstorgname=item.firstOrgName;applicantUserId=item.userid" v-text='item.empname'>
                    </li>
                </ul>
            </div>
        </yd-popup>
        <!-- <div class="last_send" v-show="sendFlag">
            <last-send :formJson="formJson" v-on:botSetflag="setFlag"></last-send>
        </div> -->
        <!-- <div class="last_send" v-show="agencyFlag">
            <agency-send :formJson="formJson" v-on:botSetflag="setFlag"></agency-send>
        </div> -->
        <!-- 总公司第一次发起 -->
        <yd-tabbar slot="tabbar" v-if="formFlag && statusPopup == false && status != 1 && orgtype == '1'&&secondFlag==false">
            <div class="assistBottom">
                <div class="examineLogo btnOne" :class="{grayColor:applicanttype==2}">
                    <yd-flexbox id="js_sendLeader" @click.native="setFlag(1)">
                        <div class="examineLogo_txt sendBtn">送领导审批</div>
                    </yd-flexbox>
                </div>
                <div class="examineLogo btnTwo" v-if="isboss=='true'">
                    <yd-flexbox id="js_sendAgent" @click.native="setFlag(4)">
                        <div class="examineLogo_txt sendBtn">送事务助理</div>
                    </yd-flexbox>
                </div>
                <div class="examineLogo btnTwo grayColor" v-if="isboss=='false'">
                    <yd-flexbox id="js_sendAgent">
                        <div class="examineLogo_txt sendBtn">提交</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
        <!-- 总公司非第一次发起 -->
        <yd-tabbar slot="tabbar" v-if="formFlag && statusPopup == false && status != 1 && orgtype == '1'&&secondFlag==true">
            <div class="assistBottom">
                <div class="examineLogo btnOne" :class="{grayColor:doApproveKey!='doApprove'}">
                    <yd-flexbox id="js_sendLeader" @click.native="setFlag(1)">
                        <div class="examineLogo_txt sendBtn">送领导审批</div>
                    </yd-flexbox>
                </div>
                <div class="examineLogo btnTwo" :class="{grayColor:travelSecrataryActivity!='travelSecrataryActivity'}" v-if="finishActivity!='finishActivity'&&isboss=='true'">
                    <yd-flexbox id="js_sendAgent" @click.native="setFlag(4)">
                        <div class="examineLogo_txt sendBtn">送事务助理</div>
                    </yd-flexbox>
                </div>
                <div class="examineLogo btnTwo" :class="{grayColor:finishActivity!='finishActivity'}" v-if="(finishActivity=='finishActivity'||pageNotice!='')&&isboss=='false'">
                    <yd-flexbox id="js_sendAgent" @click.native="finishSend()">
                        <div class="examineLogo_txt sendBtn">提交</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
        <!-- 分公司底部按钮 -->
        <yd-tabbar slot="tabbar" v-if="formFlag && orgtype == '2' && status != 1 && statusPopup == false">
            <div class="assistBottom">
                <div class="examineLogo btnOne" :class="{grayColor:applicanttype==2}">
                    <yd-flexbox id="js_sendLeader" @click.native="setFlag(1)">
                        <div class="examineLogo_txt sendBtn">送领导审批</div>
                    </yd-flexbox>
                </div>
                <div class="examineLogo btnTwo" :class="{grayColor:travelApproveActivity2!='travelApproveActivity2'}" v-if="isboss=='true'">
                    <yd-flexbox @click.native="send()">
                        <div class="examineLogo_txt sendBtn">送办公室备案</div>
                    </yd-flexbox>
                </div>
                <div class="examineLogo btnTwo" :class="{grayColor:finishActivity!='finishActivity'}" v-if="isboss=='false'">
                    <yd-flexbox @click.native="finishSend()">
                        <div class="examineLogo_txt sendBtn">提交</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'tripForm',
    data() {
        return {
            formJson: {},
            reason: '',
            confirmItem: '',
            startdate: '',
            enddate: '',
            applicantid: '',
            traveldays: '',
            travelplace: '',
            othertravelnames: '',
            selectothertravelnames: {},
            reason2: '',
            firstorgname: '',
            applicantUserId: '',
            checkNum: 999,
            secretariesNum: 999,
            statusPopup: false,
            statusPopup02: false,
            applicantname: '', // 出差人姓名
            queryTypeArr: [23, 24, 25], // 获取审批人type
            approver: [], // 审批人列表
            othertravelType: '',
            applyList: [],
            statusRadio: 0,
            statusRadio02: 99,
            popupArr: [],
            travelSecrataryActivity: '',
            addFlag: false,
            sendFlag: false,
            formFlag: true,
            agencyFlag: false,
            applicanttype: '',
            aproveList: [],
            endSelect: '',
            showTime: false,
            endText: '请选择出差结束时间',
            Edate: '',
            orgtype: '',
            rLeaderIDs: '',
            placeholder: '如有行程安排，可以参照以下格式书写： \n1月1日上午 参加启动会 \n1月1日下午，参加圆桌论坛 \n.... \n1月3日下午 结束会议返回上海，预计下午6点到达上海',
            status: this.$route.params.status,
            secondFlag: this.$route.params.secondFlag === 'true' || this.$route.params.secondFlag === true,
            errorTime: 0,
            inputDay: '',
            finishActivity: '',
            isboss: 'false',
            doApproveKey: '',
            applicantcode: '',
            travelApproveActivity2: '',
            pageNotice: '',
            queryHandlerPerson: [23, 24, 25],
            othertravelids: ''
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('出差审批');
        vm.pageRender();
        vm.select();
        let date = new Date();
        if (vm.getTravelInfo.startdate == '') {
            vm.startdate = vm.formatDate(date, 'yyyy-MM-dd');
        }
        if (vm.getTravelInfo.enddate == '') {
            vm.enddate = vm.formatDate(date, 'yyyy-MM-dd');
        }
    },
    computed: {
        ...mapGetters([
            'getUserInfo', 'getTravelInfo', 'getSearchInfo'
        ])
    },
    watch: {
        inputDay: function() {
            let vm = this;
            if (isNaN(vm.inputDay)) {
                vm.$dialog.toast({
                    mes: '出差天数请输入数字',
                    timeout: 2000
                });
                vm.traveldays = vm.inputDay;
            } else {
                vm.traveldays = vm.inputDay;
            }
        },
        approver: function() {
            let vm = this;
            vm.setTravelInfo({
                approver: vm.approver
            })
        },
        reason: function() {
            let vm = this;
            vm.setTravelInfo({
                reason: vm.reason
            })
        },
        confirmItem: function() {
            let vm = this;
            vm.setTravelInfo({
                confirmItem: vm.confirmItem
            })
        },
        startdate: function() {
            let vm = this;
            vm.setTravelInfo({
                startdate: vm.startdate
            })
        },
        enddate: function() {
            let vm = this;
            vm.setTravelInfo({
                enddate: vm.enddate
            })
        },
        applicantid: function() {
            let vm = this;
            vm.setTravelInfo({
                applicantid: vm.applicantid
            })
        },
        traveldays: function() {
            let vm = this;
            vm.setTravelInfo({
                traveldays: vm.traveldays
            })
        },
        travelplace: function() {
            let vm = this;
            vm.setTravelInfo({
                travelplace: vm.travelplace
            })
        },
        othertravelnames: function() {
            let vm = this;
            vm.setTravelInfo({
                othertravelnames: vm.othertravelnames
            })
        },
        selectothertravelnames: function() {
            let vm = this;
            vm.setTravelInfo({
                selectothertravelnames: vm.selectothertravelnames
            })
        },
        reason2: function() {
            let vm = this;
            vm.setTravelInfo({
                reason2: vm.reason2
            })
        },
        firstorgname: function() {
            let vm = this;
            vm.setTravelInfo({
                firstorgname: vm.firstorgname
            })
        },
        applicantUserId: function() {
            let vm = this;
            vm.setTravelInfo({
                applicantUserId: vm.applicantUserId
            })
            vm.isLeader(vm.applicantUserId);
        },
        statusRadio: function() {
            let vm = this;
            vm.setTravelInfo({
                statusRadio: vm.statusRadio
            })
        },
        othertravelids: function() {
            let vm = this;
            vm.setTravelInfo({
                othertravelids: vm.othertravelids
            })
        }
    },
    destroyed() {
        this.errorTime = 0;
    },
    methods: {
        ...mapMutations([
            'setTravelInfo', 'setSearchInfo'
        ]),
        select() {
            let vm = this;
            vm.othertravelids = vm.getSearchInfo.selUserId.toString();
            vm.othertravelnames = vm.getSearchInfo.selUserName.toString();
        },
        // 页面数据加载
        pageRender() {
            let vm = this;
            if (vm.secondFlag == true) {
                if (vm.getTravelInfo.jsonData != '' && vm.getTravelInfo.jsonData.workId == vm.$route.params.workItemID) {
                    // 审批流程意见
                    vm.aproveList = [...vm.getTravelInfo.jsonData.applyopinions, ...vm.getTravelInfo.jsonData.leaderopinions];
                    vm.aproveList.sort(function(a, b) {
                        let timeA = a.submitDate.replace(/-/g, '/');
                        let timeB = b.submitDate.replace(/-/g, '/');
                        return Date.parse(timeB) - Date.parse(timeA);
                    });
                    vm.isLeader(vm.getTravelInfo.jsonData.applicantcode);
                    vm.confirmItem = vm.getTravelInfo.jsonData.applicantname;
                    vm.applicantid = vm.getTravelInfo.jsonData.applicantid;
                    vm.othertravelnames = vm.getTravelInfo.jsonData.othertravelnames;
                    vm.othertravelids = vm.getTravelInfo.jsonData.othertravelids;
                    vm.travelplace = vm.getTravelInfo.jsonData.travelplace;
                    vm.traveldays = vm.getTravelInfo.jsonData.traveldays;
                    vm.reason = vm.transferString(vm.getTravelInfo.jsonData.reason);
                    vm.reason2 = vm.transferString(vm.getTravelInfo.jsonData.reason2);
                    vm.firstorgname = vm.getTravelInfo.jsonData.firstorgname;
                    vm.firstorgname = vm.getTravelInfo.jsonData.firstorgname;
                    vm.startdate = vm.formatDate(vm.getTravelInfo.jsonData.startdate, 'yyyy-MM-dd');
                    vm.enddate = vm.formatDate(vm.getTravelInfo.jsonData.enddate, 'yyyy-MM-dd');
                    vm.applicantcode = vm.getTravelInfo.jsonData.applicantcode;
                    vm.inputDay = vm.getTravelInfo.jsonData.traveldays;
                    vm.pageNotice = vm.getTravelInfo.jsonData.pageNotice;
                    vm.statusRadio = vm.getTravelInfo.statusRadio;
                    for (let key in vm.getTravelInfo.jsonData.nextBtn) {
                        if (key.indexOf('travelApproveActivity2') != -1) {
                            vm.travelApproveActivity2 = key;
                            vm.rLeaderIDs = vm.getTravelInfo.jsonData.nextBtn[key][0];
                        }
                        if (key.indexOf('travelSecrataryActivity1') != -1) {
                            vm.travelSecrataryActivity = key;
                        }
                        if (key.indexOf('travelSecrataryActivity') != -1) {
                            vm.travelSecrataryActivity = key;
                        }
                        if (key.indexOf('finishActivity') != -1) {
                            vm.finishActivity = key;
                        }
                        if (key.indexOf('doApprove') != -1) {
                            vm.doApproveKey = key;
                        }
                    }
                } else {
                    vm.getTripInit();
                }
            } else {
                vm.reason = vm.getTravelInfo.reason;
                vm.confirmItem = vm.getTravelInfo.confirmItem;
                vm.startdate = vm.getTravelInfo.startdate;
                vm.enddate = vm.getTravelInfo.enddate;
                vm.Edate = vm.getTravelInfo.enddate;
                if (vm.getTravelInfo.enddate != '') {
                    vm.endText = vm.getTravelInfo.enddate;
                }
                vm.applicantid = vm.getTravelInfo.applicantid;
                vm.traveldays = vm.getTravelInfo.traveldays;
                vm.inputDay = vm.getTravelInfo.traveldays;
                vm.travelplace = vm.getTravelInfo.travelplace;
                vm.othertravelnames = vm.getTravelInfo.othertravelnames;
                vm.othertravelids = vm.getTravelInfo.othertravelids;
                vm.selectothertravelnames = vm.getTravelInfo.selectothertravelnames;
                vm.reason2 = vm.getTravelInfo.reason2;
                vm.firstorgname = vm.getTravelInfo.firstorgname;
                vm.applicantUserId = vm.getTravelInfo.applicantUserId;
                vm.statusRadio = vm.getTravelInfo.statusRadio;
            }
            // 审批人
            if (vm.getTravelInfo.approver.length == 0) {
                vm.queryLeader();
            }
            // 申请人
            if (vm.getTravelInfo.applyList.length == 0) {
                vm.getApplyName();
            } else {
                vm.applyList = vm.getTravelInfo.applyList;
                vm.applicantname = vm.getTravelInfo.confirmItem;
            }
            // 秘书
            if (vm.getTravelInfo.secretariesList.length == 0) {
                vm.getApprover();
            }
        },
        queryLeader() {
            let vm = this;
            vm.queryHandlerPerson.map((item, index) => {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                    'type': 'travelremark',
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'queryType': item
                }, function(res) {
                    vm.approver = [...vm.approver, ...res.jsonData];
                    if (item == 25) {
                        var hash = {};
                        const newArr = vm.approver.reduceRight((item, next) => {
                            hash[next.operatorid] ? '' : hash[next.operatorid] = true && item.push(next);
                            return item
                        }, []);
                        vm.approver = newArr;
                    }
                }, function(res) {}, false);
            })
        },
        // 获取秘书
        getApprover() {
            var vm = this;
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                'type': 'travelremark',
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'queryType': 7
            }, function(res) {
                vm.setTravelInfo({
                    secretariesList: res.jsonData
                })
            }, function(res) {}, false);
        },
        isLeader(userid) {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/approveFlag', {
                'type': 'travelremark',
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'workItemID': 0,
                'personID': userid
            }, function(res) {
                // 判断是否是领导
                vm.applicanttype = res.jsonData.applicanttype;
                vm.orgtype = res.jsonData.orgtype;
                vm.isboss = res.jsonData.isboss;
            });
        },
        getApplyName() {
            let vm = this;
            let promise = new Promise(function(resolve, reject) {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                    'type': 'travelremark',
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'queryType': 27
                }, function(res) {
                    // 获取申请人列表
                    vm.applyList = res.jsonData;
                    vm.setTravelInfo({
                        applyList: vm.applyList
                    })
                    vm.applyList.map((item, index) => {
                        if (item.userid == vm.applicantcode) {
                            // vm.setTravelInfo({
                            //     statusRadio: index
                            // })
                            vm.statusRadio = index;
                            vm.confirmItem = vm.applyList[index].empname;
                            vm.applicantname = vm.applyList[index].empname;
                            vm.applicantid = vm.applyList[index].operatorid;
                            vm.firstorgname = vm.applyList[index].firstOrgName;
                            vm.firstorgname = vm.applyList[index].firstOrgName;
                            vm.applicantUserId = vm.applyList[index].userid;
                        }
                    })
                    if (vm.secondFlag == false) {
                        vm.confirmItem = vm.applyList[0].empname;
                        vm.applicantname = vm.applyList[0].empname;
                        vm.applicantid = vm.applyList[0].operatorid;
                        vm.firstorgname = vm.applyList[0].firstOrgName;
                        vm.firstorgname = vm.applyList[0].firstOrgName;
                        vm.applicantUserId = vm.applyList[0].userid;
                    }
                    resolve(true);
                });
            })
            promise.then(function(data) {
                if (vm.secondFlag == false) {
                    vm.isLeader(vm.applicantUserId);
                }
            })
        },
        getTripInit() {
            let vm = this;
            let promise = new Promise(function(resolve, reject) {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                    'type': 'travelremark',
                    'workItemID': vm.$route.params.workItemID,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
                }, function(res) {
                    if (res.jsonData.CURRENTSTATE != 10 && vm.$route.params.status == 0 && res.isSuccess == 'true') {
                        vm.$dialog.toast({
                            mes: '该流程已被处理',
                            timeout: 2000,
                            callback: () => {
                                setTimeout(function() {
                                    if (vm.$route.params.status == 0) {
                                        vm.$router.push('/handle');
                                    }
                                })
                            }
                        });
                        if (vm.$route.params.status == 0) {
                            return false;
                        }
                    }
                    if (res.isSuccess == 'false') {
                        vm.errorTime++;
                        if (vm.errorTime < 3) {
                            vm.getTripInit();
                        } else {
                            vm.$dialog.loading.close();
                            vm.$dialog.toast({
                                mes: '表单未加载成功',
                                timeout: 2000,
                                callback: () => {
                                    vm.$router.push('/handle');
                                }
                            });
                        }
                    } else {
                        resolve(true);
                        vm.$dialog.loading.close();
                    }
                    // 审批流程意见
                    vm.aproveList = [...res.jsonData.applyopinions, ...res.jsonData.leaderopinions];
                    vm.aproveList.sort(function(a, b) {
                        let timeA = a.submitDate.replace(/-/g, '/');
                        let timeB = b.submitDate.replace(/-/g, '/');
                        return Date.parse(timeB) - Date.parse(timeA);
                    });
                    // 获取表单数据
                    vm.formData = res.jsonData;
                    res.jsonData.workId = vm.$route.params.workItemID;
                    vm.setTravelInfo({
                        jsonData: res.jsonData,
                        aproveList: vm.aproveList
                    })
                    vm.confirmItem = vm.formData.applicantname;
                    vm.applicantid = vm.formData.applicantid;
                    vm.othertravelnames = vm.formData.othertravelnames;
                    vm.othertravelids = vm.formData.othertravelids;
                    vm.travelplace = vm.formData.travelplace;
                    vm.traveldays = vm.formData.traveldays;
                    vm.reason = vm.transferString(vm.formData.reason);
                    vm.reason2 = vm.transferString(vm.formData.reason2);
                    vm.firstorgname = vm.formData.firstorgname;
                    vm.firstorgname = vm.formData.firstorgname;
                    vm.startdate = vm.formatDate(vm.formData.startdate, 'yyyy-MM-dd');
                    vm.enddate = vm.formatDate(vm.formData.enddate, 'yyyy-MM-dd');
                    vm.applicantcode = vm.formData.applicantcode;
                    vm.inputDay = vm.formData.traveldays;
                    vm.pageNotice = vm.formData.pageNotice;
                    vm.setSearchInfo({
                        selUserId: vm.formData.othertravelids.split(','),
                        selUserName: vm.formData.othertravelnames.split(',')
                    })
                    if (vm.secondFlag && vm.enddate != '') {
                        vm.endText = vm.enddate;
                    }
                    for (let key in res.jsonData.nextBtn) {
                        if (key.indexOf('travelApproveActivity2') != -1) {
                            vm.travelApproveActivity2 = key;
                            vm.rLeaderIDs = res.jsonData.nextBtn[key][0];
                        }
                        if (key.indexOf('travelSecrataryActivity1') != -1) {
                            vm.travelSecrataryActivity = key;
                        }
                        if (key.indexOf('travelSecrataryActivity') != -1) {
                            vm.travelSecrataryActivity = key;
                        }
                        if (key.indexOf('finishActivity') != -1) {
                            vm.finishActivity = key;
                        }
                        if (key.indexOf('doApprove') != -1) {
                            vm.doApproveKey = key;
                        }
                    }
                });
            })
            promise.then(function(data) {
                vm.isLeader(vm.applicantcode);
                vm.getApplyName();
            })
        },
        setFlag(type) {
            let vm = this;
            let endTime = new Date(vm.enddate).getTime();
            let startTime = new Date(vm.startdate).getTime();
            let dayTime = vm.traveldays * 24 * 60 * 60 * 1000;
            if ((type == 1 && vm.applicanttype != '2' && vm.secondFlag == false) || (type == 1 && vm.secondFlag == true && vm.doApproveKey == 'doApprove')) {
                if (vm.confirmItem == '') {
                    vm.$dialog.toast({
                        mes: '请选择出差人',
                        timeout: 2000
                    });
                } else if (vm.travelplace == '') {
                    vm.$dialog.toast({
                        mes: '请输入您的出差地点',
                        timeout: 2000
                    });
                } else if (vm.enddate == '') {
                    vm.$dialog.toast({
                        mes: '请输入您的出差结束时间',
                        timeout: 2000
                    });
                } else if (isNaN(vm.traveldays)) {
                    vm.$dialog.toast({
                        mes: '出差天数请输入数字',
                        timeout: 2000
                    });
                } else if (vm.enddate != '' && (dayTime > (endTime - startTime + 24 * 60 * 60 * 1000))) {
                    vm.$dialog.toast({
                        mes: '输入时长大于实际时长',
                        timeout: 2000
                    });
                    return false;
                } else if (vm.reason == '') {
                    vm.$dialog.toast({
                        mes: '请输入出差事由',
                        timeout: 2000
                    });
                } else if (vm.reason.length > 1000) {
                    vm.$dialog.toast({
                        mes: '出差事由最大长度为1000字符',
                        timeout: 2000
                    });
                } else if (vm.reason2.length > 1000) {
                    vm.$dialog.toast({
                        mes: '行程安排最大长度为1000字符',
                        timeout: 2000
                    });
                } else {
                    if (vm.$route.params.workItemID == undefined) {
                        vm.$router.push('/lastsend/0');
                    } else {
                        vm.$router.push('/lastsend/' + vm.$route.params.workItemID);
                    }
                }
            } else if (type == 2) {
                vm.addFlag = true;
                vm.sendFlag = false;
                vm.formFlag = false;
                vm.agencyFlag = false;
            } else if (type == 3) {
                vm.addFlag = false;
                vm.sendFlag = false;
                vm.formFlag = true;
                vm.agencyFlag = false;
            } else if (vm.secondFlag == false && type == 4 && vm.isboss == 'true' || (vm.secondFlag == true && type == 4 && vm.travelSecrataryActivity == 'travelSecrataryActivity')) {
                if (vm.confirmItem == '') {
                    vm.$dialog.toast({
                        mes: '请选择出差人',
                        timeout: 2000
                    });
                } else if (vm.travelplace == '') {
                    vm.$dialog.toast({
                        mes: '请输入您的出差地点',
                        timeout: 2000
                    });
                } else if (vm.enddate == '') {
                    vm.$dialog.toast({
                        mes: '请输入您的出差结束时间',
                        timeout: 2000
                    });
                } else if (isNaN(vm.traveldays)) {
                    vm.$dialog.toast({
                        mes: '出差天数请输入数字',
                        timeout: 2000
                    });
                } else if (vm.enddate != '' && (dayTime > (endTime - startTime + 24 * 60 * 60 * 1000))) {
                    vm.$dialog.toast({
                        mes: '输入时长大于实际时长',
                        timeout: 2000
                    });
                    return false;
                } else if (vm.reason == '') {
                    vm.$dialog.toast({
                        mes: '请输入出差事由',
                        timeout: 2000
                    });
                } else if (vm.reason.length > 1000) {
                    vm.$dialog.toast({
                        mes: '出差事由最大长度为1000字符',
                        timeout: 2000
                    });
                } else if (vm.reason2.length > 1000) {
                    vm.$dialog.toast({
                        mes: '行程安排最大长度为1000字符',
                        timeout: 2000
                    });
                } else {
                    if (vm.$route.params.workItemID == undefined) {
                        vm.$router.push('/agencySend/0');
                    } else {
                        vm.$router.push('/agencySend/' + vm.$route.params.workItemID);
                    }
                }
            }
        },
        getDay(date) {
            date = date.replace(/-/g, '/');
            return (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '日';
        },
        openShowTime() {
            this.showTime = true;
            this.$refs.datetime.open();
        },
        send() {
            let vm = this;
            if (vm.travelApproveActivity2 == 'travelApproveActivity2') {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'travelremark',
                    'workItemID': vm.$route.params.workItemID,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'activityID': 'travelApproveActivity2',
                    'leaderIDs': vm.rLeaderIDs,
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.firstorgname + vm.confirmItem + '出差备案申请',
                    'ext': '{"applicantid":"' + vm.applicantid + '","applicantname":"' + vm.confirmItem + '","startdate":"' + vm.startdate + '","enddate":"' + vm.enddate + '","traveldays":"' + vm.traveldays + '","travelplace":"' + vm.travelplace + '","othertravelnames":"' + vm.othertravelnames + '","othertravelids":"' + vm.othertravelids + '","reason":"' + vm.reason + '","reason2":"' + vm.reason2 + '"}'
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '发送成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                });
            }
        },
        finishSend() {
            let vm = this;
            if (vm.finishActivity == 'finishActivity') {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'travelremark',
                    'workItemID': vm.$route.params.workItemID,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'activityID': 'finishActivity',
                    'leaderIDs': '',
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.firstorgname + vm.confirmItem + '出差备案申请',
                    'ext': ''
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '发送成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                })
            } else if (vm.pageNotice != '') {
                vm.$dialog.toast({
                    mes: vm.pageNotice,
                    timeout: 2000
                });
            }
        }
    }
}
</script>
